.container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
  width: 90%;
  margin-left: 1rem;
}

.container img {
  display: flex;
  float: right;
  margin-right: 1rem;
  margin-top: 1rem;
}

.view-share {
  display: flex;
  justify-content: space-between;
  width: 95%;
  position: relative;
  right: .5rem;

}

.icon {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.text h4 {
  position: relative;
  top: 1rem;
}